.form-control {
  --padding-start: 20px;
  --inner-padding-end: 20px;
  height: 50px;
  border: 1px solid var(--ion-color-unisa-soft-gray);
  border-radius: 25px;
}
.form-control:last-child {
  margin-bottom: 0;
}

.form-control--text {
  transition: border-color var(--ion-transition-fast);
}
.form-control--text .form-control__icon:first-of-type {
  transition: color var(--ion-transition-fast);
}
.form-control--text.ion-dirty.ion-invalid {
  border-color: var(--ion-color-unisa-danger-red);
}
.form-control--text.ion-dirty.ion-invalid .form-control__icon:first-of-type {
  color: var(--ion-color-unisa-danger-red);
}
.form-control--text.ion-valid {
  border-color: var(--ion-color-unisa-success-green);
}
.form-control--text.ion-valid .form-control__icon:first-of-type {
  color: var(--ion-color-unisa-success-green);
}

.form-submit {
  --background: var(--ion-color-unisa-orange);
}

.form-submit--button {
  --border-radius: 25px;
  height: 50px;
  width: 100%;
  font-size: 15px;
  font-weight: var(--ion-font-weight-semibold);
  text-transform: capitalize;
}

.form-select-action-sheet {
  --button-color: var(--ion-color-unisa-base);
  --button-background-activated-opacity: 0;
  --button-background-focused-opacity: 0;
  --button-background-hover-opacity: 0;
  --button-background-selected-opacity: 0;
}
.form-select-action-sheet .action-sheet-group {
  padding: 0 var(--ion-app-padding);
}
.form-select-action-sheet .action-sheet-group:first-child {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.form-select-action-sheet .action-sheet-group:last-child {
  padding-top: var(--ion-app-padding);
  padding-bottom: var(--ion-app-padding);
}
.form-select-action-sheet .action-sheet-title {
  --color: var(--ion-color-unisa-blue);
  margin-bottom: var(--ion-app-margin);
  padding-top: var(--ion-app-padding);
  padding-left: 0;
  padding-right: 0;
  font-size: 18px;
  font-weight: var(--ion-font-weight-bold);
}
.form-select-action-sheet .action-sheet-button {
  contain: initial;
  height: auto;
  margin-bottom: 30px;
  padding: 0;
  font-size: 15px;
}
.form-select-action-sheet .action-sheet-button:last-child {
  margin-bottom: 0;
}
.form-select-action-sheet .action-sheet-button.action-sheet-selected {
  color: var(--ion-color-unisa-blue);
}
.form-select-action-sheet .action-sheet-button ion-ripple-effect {
  display: none;
}
.form-select-action-sheet .action-sheet-cancel {
  height: 50px;
  background: var(--ion-color-unisa-orange);
  border-radius: 25px;
  font-size: 16px;
  font-weight: var(--ion-font-weight-semibold);
}
.form-select-action-sheet .action-sheet-cancel span {
  color: white;
  justify-content: center;
}

.form-picker {
  color: var(--ion-color-unisa-base);
}
.form-picker .picker-wrapper {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.form-picker .picker-button {
  text-transform: capitalize !important;
}
.form-picker .picker-opt-selected, .form-picker .picker-button {
  color: var(--ion-color-unisa-blue) !important;
  font-weight: var(--ion-font-weight-semibold) !important;
}

.app-list {
  width: 100%;
  padding: 0;
  background: none;
}
.app-list ion-list-header {
  padding: 0;
  margin-bottom: 20px;
}
.app-list ion-list-header ion-label {
  color: var(--ion-color-unisa-blue);
  font-size: 20px;
  font-weight: var(--ion-font-weight-semibold);
  text-align: left;
}
.app-list ion-item {
  --background: white;
  --padding-bottom: 20px;
  --padding-end: 20px;
  --padding-start: 20px;
  --padding-top: 20px;
  margin-bottom: 20px;
  border-radius: 15px;
  box-sizing: border-box;
  cursor: pointer;
  box-shadow: 0 5px 40px -20px rgba(0, 0, 0, 0.2);
}
.app-list ion-item:last-child {
  margin-bottom: 0;
}
.app-list ion-item ion-label h3 {
  padding-right: 20px;
  margin-bottom: 8px;
  color: var(--ion-color-unisa-blue);
  font-size: 16px;
  font-weight: var(--ion-font-weight-semibold);
  text-transform: lowercase;
}
.app-list ion-item ion-label h3:first-letter {
  text-transform: capitalize;
}
.app-list ion-item ion-label div {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  font-size: 14px;
}
.app-list ion-item ion-label div:last-child {
  margin-bottom: 0;
}
.app-list ion-item ion-label div div {
  display: flex;
  flex-direction: column;
}
.app-list ion-item ion-label div div span:not(:last-child) {
  margin-bottom: 5px;
}
.app-list ion-item ion-label div span {
  margin-left: 8px;
}

.page-content {
  --overflow: hidden;
}

.page-main {
  overflow-y: auto;
  flex-grow: 1;
  width: calc(100% - 30px);
  padding: var(--ion-app-padding);
  background: var(--ion-color-unisa-soft-blue);
  border-top-right-radius: 25px;
}
.page-main::-webkit-scrollbar {
  display: none;
}

.page-title {
  color: var(--ion-color-unisa-blue);
  font-size: 24px;
  font-weight: var(--ion-font-weight-bold);
}