    /** UNISA blue **/
    --ion-color-unisa-blue: #2f577f;
    --ion-color-unisa-blue-rgb: 47, 87, 127;
    --ion-color-unisa-blue-contrast: #ffffff;
    --ion-color-unisa-blue-contrast-rgb: 255, 255, 255;
    --ion-color-unisa-blue-shade: #294d70;
