    --ion-color-unisa-soft-orange-contrast-rgb: 0, 0, 0;
    --ion-color-unisa-soft-orange-shade: #e0d3c6;
    --ion-color-unisa-soft-orange-tint: #fff2e4;

    /** UNISA soft gray **/
    --ion-color-unisa-soft-gray: #e6e6e6;
