    --ion-color-unisa-success-green-contrast: #000000;
    --ion-color-unisa-success-green-contrast-rgb: 0, 0, 0;
    --ion-color-unisa-success-green-shade: #6bbf64;
    --ion-color-unisa-success-green-tint: #87dd80;

    /** UNISA success dark green **/
