    /** UNISA warning yellow **/
    --ion-color-unisa-warning-yellow: #ffc107;
    --ion-color-unisa-warning-yellow-rgb: 255, 193, 7;
    --ion-color-unisa-warning-yellow-contrast: #000000;
    --ion-color-unisa-warning-yellow-contrast-rgb: 0, 0, 0;
    --ion-color-unisa-warning-yellow-shade: #e0aa06;
