    --ion-color-unisa-danger-red: #ff795b;
    --ion-color-unisa-danger-red-rgb: 255, 121, 91;
    --ion-color-unisa-danger-red-contrast: #000000;
    --ion-color-unisa-danger-red-contrast-rgb: 0, 0, 0;
    --ion-color-unisa-danger-red-shade: #e06a50;
    --ion-color-unisa-danger-red-tint: #ff866b;

    /** UNISA danger dark red **/
    --ion-color-unisa-danger-dark-red: #ff5252;
    --ion-color-unisa-danger-dark-red-rgb: 255, 82, 82;
