app-modal {
  height: 100%;
  display: flex;
  flex-direction: column;
}

ion-router-outlet {
  background: var(--ion-background-color);
}

ion-toolbar {
  --min-height: none;
  --background: none;
}

ion-title {
  padding: 0;
}

ion-item {
  --padding-start: 0;
  --inner-padding-end: 0;
  --background: none;
  --background-hover: none;
  --background-activated: transparent;
  --highlight-background: none;
  --ripple-color: transparent;
  --border-width: 0;
  --inner-border-width: 0;
}

ion-buttons {
  margin: 0;
}

ion-menu-button {
  --padding-start: 0;
  --padding-end: 0;
  --ripple-color: transparent;
  width: auto;
  height: auto;
}

ion-button {
  --box-shadow: none;
  --ripple-color: transparent;
  --background-hover-opacity: 0;
}
ion-button.button {
  margin: 0;
}
ion-button::part(native) {
  padding: 0;
}

ion-checkbox {
  margin: 0;
  --background-checked: var(--ion-color-unisa-blue);
  --border-color: var(--ion-color-unisa-soft-gray);
  --border-color-checked: var(--ion-color-unisa-blue);
}

ion-radio {
  --color: var(--ion-color-unisa-soft-gray);
  --color-checked: var(--ion-color-unisa-blue);
  margin-right: 15px;
}

ion-thumbnail {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  margin: 0;
  background: var(--ion-color-unisa-soft-blue);
  border-radius: 15px;
  color: var(--ion-color-unisa-blue);
}

ion-label {
  margin: 0;
}

ion-chip {
  margin: 0;
  padding: 5px 10px;
}
ion-chip ion-label {
  text-transform: capitalize;
}

ion-toggle {
  --handle-width: 16px;
  --handle-max-height: 16px;
  --handle-spacing: 2px;
  --handle-box-shadow: none;
  --background: var(--ion-color-unisa-soft-gray);
  --background-checked: var(--ion-color-unisa-blue);
  height: 20px;
  width: 36px;
}

.container-flex {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  align-self: center;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  --padding-start: 30px;
  --padding-end: 30px;
  padding: 0 30px;
  box-sizing: border-box;
}

.app-loading {
  color: var(--ion-color-unisa-blue);
}

.app-icon {
  color: var(--ion-color-unisa-blue);
  font-size: 20px;
  vertical-align: middle;
}

.app-icon-left {
  margin-right: 10px;
}

.app-icon-right {
  margin-left: 10px;
}

.app-toast {
  --border-radius: 5px;
  --button-color: white;
  color: white;
  font-weight: 400;
}

.flex {
  display: flex;
  height: 100%;
}

.flex-direction-column {
  flex-direction: column;
}

@keyframes unisa-animation {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(0.75);
    opacity: 0.5;
  }
}
.unisa {
  color: #2f577f;
  font-family: "Spartan", sans-serif;
  font-size: 24px;
  font-weight: 600;
}
.unisa .unisa-letter {
  display: inline-block;
  animation-name: unisa-animation;
  animation-duration: 1500ms;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.unisa .unisa-letter:nth-child(1) {
  animation-delay: 0;
}
.unisa .unisa-letter:nth-child(2) {
  animation-delay: 250ms;
}
.unisa .unisa-letter:nth-child(3) {
  animation-delay: 500ms;
}
.unisa .unisa-letter:nth-child(4) {
  animation-delay: 750ms;
}
.unisa .unisa-letter:nth-child(5) {
  animation-delay: 1000ms;
}